.dialogs__wrapper {
  color: white;
  display: grid;
  grid-template-columns: 4fr 10fr;
  grid-gap: 20px;
  padding-right: 20px;
  padding-top: 40px;
}

.dialogs {
  display: grid;
  grid-template-rows: repeat(auto-fill, minmax(60px, auto));
}

.dialog {
  display: flex;
  align-items: center;
  padding: 10px 5px;
  text-decoration: none;
  color: purple;
  text-shadow: -0.5px -0.5px #232323;
}

.name {
  margin-left: 10px;
}

.active {
  color: gold;
}

.messages {
  display: grid;
  grid-template-rows: repeat(auto-fill, minmax(50px, auto));
}

.message {
  display: grid;
  align-content: center;
  margin: 5px 0;
}

.outgoing {
  justify-content: end;
}

.incoming {
  justify-content: start;
}

.messageWrap {
  background: rgba(50, 50, 50, 0.2);
  padding: 10px 10px;
  border-radius: 5px;
}

.newMessage {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.messagesWrapper {
  padding-bottom: 30px;
}

.textarea {
  background: rgba(200, 200, 200, 0.5);
  outline: none;
  border: none;
  border-radius: 10px;
  padding: 5px;
  min-height: 100px;
  margin-top: 30px;
  color: white;
  font-size: 14px;
}

.sendButton {
  width: 80px;
  align-self: flex-end;
  outline: none;
  background: #BADA55;
  font-weight: bold;
  border: none;
  font-size: 15px;
  color: white;
  border-radius: 5px;
  margin-top: 10px;
  box-shadow: 5px 5px 5px rgba(200, 200, 200, 0.5);
}
